body {
  font-family: "Trebuchet MS", Helvetica, sans-serif;
  font-size: calc(3vw + 3vh);
  color: #35445c;
}

.temp-text {
  height: 90vh;
  line-height: 90vh;
  text-align: center;
}

.game-wrapper {
  width: 100%;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  background-color: #b9d8fd;
}

.game-wrapper-overlay {
  position: absolute;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(52, 53, 55, 0.54);
  z-index: 100;
}

.init-button {
    width: 25vw;
    height: 7vh;
}

.game-wrapper_menu-page {
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
}

.game-menu {
  list-style-type: none;
  padding: 0;
}

.menu-option {
  margin-top: 5vh;
}

.menu-link {
  text-decoration: none;
  color: #35445c;
}

.menu-option:first-child {
  margin: 0;
}

.header {
  height: 10vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-item-align: end;
      align-self: flex-end;
  -ms-flex-align: center;
      align-items: center;
  padding-right: 6vw;
}

.close-btn {
  position: relative;
  display: block;
  width: 4vw;
  height: 4vw;
}

.close-btn-image {
  width: 100%;
  height: auto;
  position: absolute;
}

.success-chart-capture {
  font-size: 3vh;
  margin-bottom: 2vh;
  text-align: center;
}

.rc-progress-circle {
  height: 30vh;
}

.question-field {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  margin: 2vh;
  font-size: 3vh;
  color: #3463af;
}

.keyboard {
  width: 100%;
}

.keyboard-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  width: 100%;
  -ms-flex-pack: evenly;
      justify-content: evenly;
}

.keyboard-cell {
  width: 33%;
  height: 10vh;
  text-align: center;
  line-height: 10vh;
  border: 0.5px solid #d6dde7;
  font-size: calc(2vw + 2vh);
}

/*SETTINGS*/

.settings-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-size: calc(2vh + 2vw);
}

.settings-buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  font-size: calc(2vh + 2vw);
  -ms-flex-pack: center;
      justify-content: center;
}

.settings-selected {
  margin: 0 2vw;
}





